<template>
  <div>
    <banner></banner>
    <about></about>
    <especialidades></especialidades>
    <formacoes></formacoes>
    <testemunhos></testemunhos>
    <parceiros></parceiros>
    <equipa></equipa>
    <contactos></contactos>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./home/Banner";
import About from "./home/About";
import Especialidades from "./home/Especialidades";
import Formacoes from "./home/Formacoes";
import Testemunhos from "./home/Testemunhos";
import Parceiros from "./home/Parceiros";
import Equipa from "./home/Equipa";
import Contactos from "./home/Contactos";
import FadeIn from "../animations/fade";

export default {
  data() {
    return {
      FadeIn
    };
  },
  components: {
    banner: Banner,
    about: About,
    especialidades: Especialidades,
    formacoes: Formacoes,
    testemunhos: Testemunhos,
    parceiros: Parceiros,
    equipa: Equipa,
    contactos: Contactos
  },
  metaInfo() {
    return {
      title: "Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        },
        {
          property: "og:description",
          content: "Ana Carolina Pereira - Clínica de Psicologia"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
</style>



